@import 'scss/generic/_reset.scss';
@import 'scss/settings/_mixins.scss';
@import 'scss/settings/_variables.scss';
@import 'scss/elements/_base.scss';
@import 'scss/elements/_buttons.scss';
@import 'scss/elements/_banner.scss';
@import 'scss/elements/_informations.scss';
@import 'scss/elements/_benefits.scss';

a {
  color: $color-primary;
}

.header .container {
  background-color: #fff;
  box-shadow: 1px 1px 4px 0 rgba(0,0,0,.1);
  position: fixed;
  width: 100%;
  z-index: 3;
}

.header ul {
  margin: 0;
  padding: 0;
  list-style: none;
  overflow: hidden;
  background-color: #fff;
}

.header li a {
  display: block;
  padding: 20px 20px;
  border-right: 1px solid #f4f4f4;
  text-decoration: none;
  font-family: $font-raleway;
  font-weight: 300;
}

.header li a:hover,
.header .menu-btn:hover {
  background-color: #f4f4f4;
}

.header .logo {
  display: block;
  float: left;
  font-size: 2em;
  padding: 10px 20px;
  text-decoration: none;
  font-family: $font-raleway;
  font-weight: 300;
}

/* menu */

.header .menu {
  clear: both;
  max-height: 0;
  transition: max-height .2s ease-out;
}

/* menu icon */

.header .menu-icon {
  cursor: pointer;
  display: inline-block;
  float: right;
  padding: 28px 20px;
  position: relative;
  user-select: none;
}

.header .menu-icon .navicon {
  background: $color-primary;
  display: block;
  height: 2px;
  position: relative;
  transition: background .2s ease-out;
  width: 18px;
}

.header .menu-icon .navicon:before,
.header .menu-icon .navicon:after {
  background: $color-primary;
  content: '';
  display: block;
  height: 100%;
  position: absolute;
  transition: all .2s ease-out;
  width: 100%;
}

.header .menu-icon .navicon:before {
  top: 5px;
}

.header .menu-icon .navicon:after {
  top: -5px;
}

/* menu btn */

.header .menu-btn {
  display: none;
}

.header .menu-btn:checked ~ .menu {
  max-height: 240px;
}

.header .menu-btn:checked ~ .menu-icon .navicon {
  background: transparent;
}

.header .menu-btn:checked ~ .menu-icon .navicon:before {
  transform: rotate(-45deg);
}

.header .menu-btn:checked ~ .menu-icon .navicon:after {
  transform: rotate(45deg);
}

.header .menu-btn:checked ~ .menu-icon:not(.steps) .navicon:before,
.header .menu-btn:checked ~ .menu-icon:not(.steps) .navicon:after {
  top: 0;
}

/* 48em = 768px */

@media (min-width: 48em) {
  .header li {
    float: left;
  }
  .header li a {
    padding: 20px 30px;
  }
  .header .menu {
    clear: none;
    float: right;
    max-height: none;
  }
  .header .menu-icon {
    display: none;
  }
}


.pricing {
  h2 {
    text-align: center;
    font-family: $font-raleway;
    font-size: 3rem;
    margin-top: 15px;
  }

  &__boxes {
    margin-top: 50px;
  }

  &__box {
    //width: 370px;
    display: flex;
    align-items: center;
    justify-content: center;
    border-radius: 18px;
    margin: 40px 40px;

    &__basic {
      border: 1px solid $color-redish;
    }

    &__professional {
      border: 1px solid $color-primary;
      //width: 400px;
      //margin: 0 40px;
      box-shadow: 5px 1px 27px $color-shadow;
    }

    &__premium {
      border: 1px solid $color-green;
    }
  }
    &__details {
      margin-bottom: 60px;
      margin-top: 34px;
      text-align: center;

      .pricing__name {
        font-family: $font-raleway;
        font-weight: 700;
        font-size: 1.25rem;
      }

      .pricing__price {
        margin-top: 25px;
        font-size: 3rem;
        font-family: $font-open-sans;
        font-weight: 300;

        &__basic {
          color: $color-redish;
          font-size: 2.5rem;
        }

        &__professional {
          color: $color-primary;
          font-size: 2.5rem;
        }

        &__premium {
          color: $color-green;
          font-size: 2.5rem;
        }
      }
    }

    &__items {
      text-align: center;
      font-family: $font-open-sans;

      .pricing_item::before {
        content: "";
        width: 11px;
        height: 11px;
        display: inline-block;
        margin-right: 10px;
        background-image: url("../images/Checkmark.svg");
      }

      .pricing_item_red::before {
        content: "";
        width: 11px;
        height: 11px;
        display: inline-block;
        margin-right: 10px;
        background-image: url("../images/Close.svg");
      }
    }
  }





